<template>
  <div class="shopping-mall">
    <router-link to="/shop-register-page">  </router-link>
    <div class="products-container">
      <div
        v-if="!products || (Array.isArray(products) && products.length === 0)"
      >
           !
      </div>
      <div v-else v-for="(product, index) in products" :key="product.productId">
        <h2>{{ product.name }}</h2>
        <carousel>
          <img
            v-for="(imageData, index) in product.imageDataList"
            :key="index"
            :src="
              'data:image/jpeg;base64,' +
              btoa(String.fromCharCode(...new Uint8Array(imageData.data)))
            "
          />
        </carousel>

        <v-container>
          <v-row>
            <v-col v-for="image in images" :key="image" cols="3">
              <v-img :src="image" aspect-ratio="1" class="grey lighten-2">
                <template v-slot:placeholder>
                  <v-row
                    class="fill-height ma-0"
                    align="center"
                    justify="center"
                  >
                    <v-progress-circular indeterminate color="grey lighten-5" />
                  </v-row>
                </template>
              </v-img>
            </v-col>
          </v-row>
        </v-container>
        <p>{{ product.description }}</p>
        <p>{{ product.price }}</p>
        <button @click="addToCart(product)"> </button>
        <router-link
          :to="{ name: 'ShopModifyPage', params: { productId: index } }"
        >
           
        </router-link>
      </div>
    </div>
    <div class="cart-container">
      <h2>Cart</h2>
      <ul>
        <li v-for="(item, index) in cart" :key="index">
          {{ item.name }} - Quantity: {{ item.quantity }} - Total: won{{
            item.price * item.quantity
          }}
        </li>
      </ul>
      <p>Total: ${{ total }}</p>
    </div>
  </div>
</template>

<script>
import Carousel from 'vue-carousel';
export default {
  name: 'ShopList',
  components: {
    Carousel,
  },
  props: {
    products: {
      type: Array,
    },
  },
  data() {
    return {
      cart: [],
      images: [
        require('@/assets/uploadImgs/link.jpg'),
        require('@/assets/uploadImgs/mario_game.jpg'),
        require('@/assets/uploadImgs/mario.png'),
      ],
    };
  },
  computed: {
    total() {
      let sum = 0;
      for (let item of this.cart) {
        sum += item.price * item.quantity;
      }
      return sum;
    },
  },
  methods: {
    addToCart(product) {
      let index = this.cart.findIndex((item) => item.name === product.name);
      if (index === -1) {
        this.cart.push({ ...product, quantity: 1 });
      } else {
        this.cart[index].quantity += 1;
      }
    },
    getImageUrl(byteArray) {
      const blob = new Blob([byteArray]);
      return URL.createObjectURL(blob);
    },
  },
};
</script>

<style>
.shopping-mall {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.products-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}
.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 200px;
}
.cart-container {
  margin-top: 50px;
}
</style>
