<template>
  <v-container>
    <div>{{ currentTurnShape }}  .</div>
    <table-component
      :table-data="tableData"
      :current-turn-shape="currentTurnShape"
      @updateTurnShape="updateTurnShape"
      @updateWinner="updateWinner"
      @updateTableData="updateTableData"
    />
    <div v-if="winner">{{ winner }}  !</div>
  </v-container>
</template>

<script>
import axios from 'axios';
import TableComponent from '@/components/lecture/emitTest/TableComponent.vue';

//  
// ,      .
//     ?
// 'O', 'X'   !

export default {
  name: 'EmitTestBoardGame',
  components: {
    TableComponent,
  },
  data() {
    return {
      currentTurnShape: 'O',
      // tableData[5][5]
      tableData: [
        ['', '', '', '', ''],
        ['', '', '', '', ''],
        ['', '', '', '', ''],
        ['', '', '', '', ''],
        ['', '', '', '', ''],
      ],
      winner: '',
    };
  },
  methods: {
    updateTurnShape(passingValue) {
      console.log(
        'SimpleBoardGameView received TableComponent info: ' + passingValue
      );
      this.currentTurnShape = passingValue;
    },
    updateWinner(receivedWinner) {
      console.log('Main Board Game: winner received');
      this.winner = receivedWinner;
      axios
        .post('http://localhost:7777/vue/first/winner-receive-test', {
          winner: this.winner,
        })
        .then(() => {
          console.log('  !');
        })
        .catch(() => {
          alert('problem occured!');
        });
    },
    updateTableData() {
      this.tableData = [
        ['', '', '', '', ''],
        ['', '', '', '', ''],
        ['', '', '', '', ''],
        ['', '', '', '', ''],
        ['', '', '', '', ''],
      ];
    },
  },
};
</script>

