<template>
    <v-container>
      <v-data-table 
          :headers="headerTitle" 
          :items="boards"
          :items-per-page="10"
          @click:row="readRow"
          show-select
          class="elevation-1"
          item-key="boardId"
          v-model="selectedItems"/>
    </v-container>
  </template>
  
  <script>
  
  import router from "@/router"
  import { mapActions, mapState } from 'vuex'
  
  export default {
      name: "TableExercisePage",
      data () {
          return {
              headerTitle: [
                  { text: '', value: 'boardId', width: "70px" },
                  { text: '', value: 'title', width: "200px" },
                  { text: '', value: 'writer', width: "100px" },
                  { text: '', value: 'regDate', width: "100px" },
              ],
              selectedItems: [],
              //boardsWithSelected: [this.boards.map(x => ({...x, isSelectable: false}))],
          }
      },
      computed: {
          ...mapState([
              'boards'
          ]),
      },
      mounted () {
          this.requestBoardListToSpring()
      },
      methods: {
          ...mapActions([
              'requestBoardListToSpring'
          ]),
          readRow (readValue) {
              alert(" : " + JSON.stringify(readValue))
  
              router.push({
                  name: 'JpaBoardReadPage',
                  params: { boardId: readValue.boardId.toString() }
              })
          },
      }
  }
  
  </script>
  
