<template>
  <form @submit.prevent="onSubmit">
    <table>
      <tr>
        <td></td>
        <td>
          <input type="text" v-model="memberName"/>
        </td>
      </tr>

      <tr>
        <td></td>
        <td>
          <input type="text" v-model="major"/>
        </td>
      </tr>
    </table>

    <div>
      <button type="submit"></button>
    </div>
  </form>
</template>

<script>

export default {
    name: "AxiosTestForm",
    data () {
        return {
            memberName: ' ',
            major: ' ',
        }
    },
    methods: {
        onSubmit () {
            const { memberName, major } = this
            this.$emit('submit', { memberName, major })
        }
    }
}

</script>

