<template>
  <v-container>
    <div>
      <p>{{ count }}  !</p>
      <button @click="increment"></button>
    </div>
  </v-container>
</template>

<script>
export default {
  name: 'ClickEventTest',
  data() {
    return {
      count: 0,
    };
  },
  methods: {
    increment() {
      this.count += 1;
    },
  },
};
</script>
