<template>
    <v-container>
      <div>
        <button v-on:click="addManyMonsters">  </button><br/><br/>
  
        <button v-on:click="darkFlame"> </button><br/>
  
        <ul>
          <li v-for="(item, index) in list"
              v-bind:key="index">
            ID: {{ item.id }} {{ item.name }} HP: {{ item.hp }}
            <button v-on:click="attackMonster(index)"></button>
          </li>
        </ul>
  
        <ul>
          <li v-for="(item, index) in list"
              v-bind:key="index">
            ID: {{ item.id }} {{ item.name }} HP: {{ item.hp }}
            <button v-on:click="attackMonster(index)"></button>
          </li>
        </ul>
      </div>
    </v-container>
  </template>
  
