  export default {
    name: "LifeCyclePage",
    data () {
      return {
        monsterBook: [
          { monsterId: 1, name: '', hp: 50, exp: 10, dropMoney: 5 },
          { monsterId: 2, name: '', hp: 80, exp: 15, dropMoney: 10 },
          { monsterId: 3, name: '', hp: 100, exp: 25, dropMoney: 10 },
          { monsterId: 4, name: '', hp: 100, exp: 15, dropMoney: 25 },
          { monsterId: 5, name: '', hp: 150, exp: 15, dropMoney: 25 },
          { monsterId: 6, name: '', hp: 150, exp: 15, dropMoney: 25 },
          { monsterId: 7, name: '', hp: 150, exp: 15, dropMoney: 25 },
          { monsterId: 8, name: '', hp: 150, exp: 15, dropMoney: 25 },
          { monsterId: 9, name: '', hp: 150, exp: 15, dropMoney: 25 },
          { monsterId: 10, name: '', hp: 150, exp: 15, dropMoney: 25 },
          { monsterId: 11, name: '', hp: 150, exp: 15, dropMoney: 25 },
          { monsterId: 12, name: '', hp: 150, exp: 15, dropMoney: 25 },
          { monsterId: 13, name: '', hp: 150, exp: 15, dropMoney: 25 },
          { monsterId: 14, name: ' ', hp: 150, exp: 15, dropMoney: 25 },
          { monsterId: 15, name: '', hp: 150, exp: 15, dropMoney: 25 },
          { monsterId: 16, name: ' ', hp: 150, exp: 15, dropMoney: 25 },
          { monsterId: 17, name: '', hp: 150, exp: 15, dropMoney: 25 },
          { monsterId: 18, name: '', hp: 150, exp: 15, dropMoney: 25 },
          { monsterId: 19, name: '', hp: 150, exp: 15, dropMoney: 25 },
          { monsterId: 20, name: '', hp: 150, exp: 15, dropMoney: 25 },
          { monsterId: 21, name: '', hp: 150, exp: 15, dropMoney: 25 },
  
          { monsterId: 999, name: '', hp: 150, exp: 15, dropMoney: 25 },
          { monsterId: 1000, name: ' ', hp: 150, exp: 15, dropMoney: 25 },
          { monsterId: 1001, name: ' ', hp: 150, exp: 15, dropMoney: 25 },
          { monsterId: 1002, name: ' ', hp: 150, exp: 15, dropMoney: 25 },
          { monsterId: 1003, name: ' ', hp: 150, exp: 15, dropMoney: 25 },
          { monsterId: 1004, name: ' ', hp: 150, exp: 15, dropMoney: 25 },
          { monsterId: 1005, name: ' ', hp: 150, exp: 15, dropMoney: 25 },
          { monsterId: 1005, name: ' ', hp: 150, exp: 15, dropMoney: 25 },
          { monsterId: 1007, name: ' ', hp: 150, exp: 15, dropMoney: 25 },
        ],
        list: [
          { id: 1, name: '', hp: 100 },
          { id: 2, name: '', hp: 200 },
          { id: 3, name: '', hp: 150 },
          { id: 4, name: '', hp: 250},
          { id: 5, name: '', hp: 100 },
        ],
      }
    },
    methods: {
      addManyMonsters: function () {
        for (let i = 0; i < 100; i++) {
          let max = this.list.reduce(function(a, b) {
            return a > b.id ? a : b.id
          }, 0)
  
          let idx = Math.floor(Math.random() * this.monsterBook.length)
  
          this.list.push({
            id: max + 1,
            monsterId: idx,
            name: this.monsterBook[idx].name,
            hp: this.monsterBook[idx].hp
          })
        }
      },
      darkFlame () {
        console.log(" ")
  
        for (let i = 0; i < this.list.length; i++) {
          this.list[i].hp = BigInt(this.list[i].hp) - BigInt(1000)
        }
      },
      killMonster () {
        for (let i = 0; i < this.list.length; i++) {
          if (this.list[i].hp <= 0) {
            this.list.splice(i, 1)
          }
        }
      }
    },
    beforeCreate: function () {
      alert('Before Create')
    },
    created: function () {
      alert('Created')
    },
    beforeMount: function () {
      alert('Before Mount')
    },
    mounted: function () {
      alert('Mounted')
    },
    beforeUpdate () {
      alert('Before Update')
  
      this.killMonster();
    },
    updated: function () {
      alert('Updated')
    },
    beforeDestroy: function () {
      alert('Before Destroy')
    },
    destroyed: function () {
      alert('Destroyed')
    }
  }
  </script>
  
